<template>
    <div class="newsInfo-container">
            <!-- <h3> ---- {{ id }}</h3>
            <h3> ---- {{ $route.params }}</h3> -->
            <h3 class="title">{{ newsInfo.title }}</h3>
        <p class="subtitle">
            <span>{{ newsInfo.add_time | dateFormat("YYYY-MM-DD HH:mm")}}</span>
            <span>{{ newsInfo.click }}</span>
        </p>

        <hr>

        <div class="content" v-html="newsInfo.content"></div>
        <div class="content" v-html="cat1"></div>
        <!-- <div class="content" :url="newsInfo.content"></div> -->
        <comment-box  :id="this.id"></comment-box>

    </div>
</template>

<script>
import comment from "../subComponents/Comment.vue"

import { Toast } from "mint-ui";
export default {
    data(){
        return {
            id: this.$route.params.id,

            newsInfo:{}, //

           // cat:"<img src='//www.w3cplus.com/sites/default/files/blogs/2017/1709/cat.jpg' />"
           cat:"<html><img src='//www.w3cplus.com/sites/default/files/blogs/2017/1709/cat.jpg' /> </html>"
           ,
           cat1:"<html><body><p><img src='/i/ct_netscape.jpg'/></p><p> W3School.com.cn <img src='http://www.w3school.com.cn/i/w3school_logo_white.gif' /></p></body></html>"
        };
    },
    created(){
        this.getNewsInfoData();
    },
    methods:{
        getNewsInfoData(){//
            this.$http.get("newInfoData.txt?"+  Math.random()).then(result =>{
                if(result.body.status === 0){
                    for (var i=0; i < result.body.message.length;i++)
                    {
                        if(this.id == result.body.message[i].id)
                        {
                            this.newsInfo = result.body.message[i];
                            return;
                        } 
                    }
                    this.newsInfo = result.body.message[0];
                    
                    //Toast("")
                }else{
                    Toast("")
                }
        })}
    }
    ,
    components:{
        "comment-box" : comment
    }
}
</script>


<!-- <style lang="scss" scoped>     .content   img     
                                  v-html   img  scoped  
                                  ~~  -->
<style lang="scss">
.newsInfo-container{
    padding: 0 4px;
    .title{
        font-size: 16px;  
        text-align: center;
        margin: 15px 0;
        color: red;
    }
    .subtitle{
        font-size: 13px;
        color: #226aff;
        // 
        display: flex;
        justify-content: space-between;

    }
    .content{
        img{
            width: 100%;
        }
    }
}
</style>


