<template>
    <div class="cmt-container">
        <h3></h3>
        <hr>
        <textarea placeholder="BB(120)"
            maxlength="120" v-model="msg"></textarea>
        <mt-button type="primary" size="large" @click="postComment"></mt-button>

        <div class="cmt-list">
            <div class="cmt-item" v-for="(item, i) in commentInfo" :key="i">
                <div class="cmt-title">
                    <!-- {{item.id}} {{i + 1}}&nbsp;&nbsp;{{item.user_name}}&nbsp;&nbsp;{{item.add_time | dateFormat("YYYY-MM-DD HH:mm")}} -->
                    {{i + 1}}&nbsp;&nbsp;{{item.user_name}}&nbsp;&nbsp;{{item.add_time | dateFormat("YYYY-MM-DD HH:mm")}}
                </div>
                <div class="cmt-body">
                    {{ item.content === "undefined" ? "" : item.content}}
                </div>
            </div>
            <!--  -->
             <!-- <div class="cmt-item">
                <div class="cmt-title">
                    &nbsp;&nbsp;&nbsp;&nbsp;2012-12-12 12:12:12
                </div>
                <div class="cmt-body">
                    
                </div>
            </div>

             <div class="cmt-item">
                <div class="cmt-title">
                    &nbsp;&nbsp;&nbsp;&nbsp;2012-12-12 12:12:12
                </div>
                <div class="cmt-body">
                    
                </div>
            </div> -->
            <!--  -->
        </div>

        <mt-button type="danger" size="large" plain @click="this.getMoreCommentInfoData"></mt-button>
    </div>
</template>

<script>
import{ Toast } from "mint-ui";
export default {
    data() {
        return {
            pageIndex: 1 ,//
            commentInfo: [],
            msg:''
        }
    },
     created(){
        this.getCommentInfoData();
    },
    methods: {
         getCommentInfoData(){
             this.$http.get("commentData_" + this.pageIndex + ".json?"+  Math.random()).then(result => {
                //  result.setContentType("text/html;charset=UTF-8");  
                  if(result.body.status === 0){
                    // this.commentInfo = result.body.message;
                    this.commentInfo = this.commentInfo.concat(result.body.message);
                    Toast('');
                    console.log( result.body.message)
                  }
                  else{
                    console.warn("");
                    Toast('');
                }
             })
         },
         getMoreCommentInfoData(){
             this.pageIndex++;
             this.getCommentInfoData();
         },
         postComment()
         {  
             let _msg = this.msg.trim();
             if(_msg.length === 0){
                 Toast('');
                 return;
             }
             // 

            var cmt = {
                        user_name: "",
                        add_time: Date.now(),
                        content: _msg
                    };
             this.commentInfo.unshift(cmt);
             this.msg = '';
             return;

             //   
             // ID = $route.params.id;
             // 1 URL
             // 2 content: this.msg
             // 3 , ( emulateJSON:true ) 
             this.$http
             .post('api/postcomment/' + this.$route.params.id,
                { content:_msg })
             .then(function(result) {
                 if(result.body.status === 0){
                     //1.
                     var cmt = {
                         user_name: "",
                         add_time: Date.now(),
                         content: _msg
                     };
                     this.commentInfo.unshift(cmt);
                     this.msg = '';
                 }
             });
         }
     },
     props: ['id']
}
</script>


<style lang="scss" scoped>
    .cmt-container{
        h3{
            font-size: 18px;
        }
        textarea{
            font-size: 14px;
            height: 85px;
            margin: 0;
        }

        .cmt-list{
            margin: 5px 0;
            .cmt-item
            {
                font-size: 13px;
                .cmt-title
                {
                    line-height: 30px;
                    background-color: #ccc;
                }
                .cmt-body
                {
                    line-height: 35px;
                    text-indent: 2em;
                }
            }
        }
    }
</style>



